import { Storage } from '@ionic/storage';import { Injectable } from '@angular/core';
import { HttpErrorResponse, HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { BadInput } from '../_helpers/bad-input';
import { NotFoundError } from '../_helpers/not-found-error';
import { UnautorizedError } from '../_helpers/Unauthorized-error';

import { AppError } from '../_helpers/app-error';
import { PersonelModel, GetMesaiModel, IDT_IK_MESAI_KAYIT_HAZIRLIK_Model, sefOnayModel, Birlesik } from '../_models/UserModel';


@Injectable({
  providedIn: 'root'
})
export class PuantajService {
  //readonly rootUrl = 'http://78.189.86.247:118/';
  readonly rootUrl = 'http://92.45.19.226:80/';
  // http://78.189.86.247 : 8085/
 // http://92.45.19. 226 :80
  currentuser:PersonelModel;
  private handleError(error: HttpErrorResponse) {
  
console.log(error)

    if (error.status === 400)


    return Observable.throw(new BadInput(error.error.Message));

    if (error.status === 404)
      return Observable.throw(new NotFoundError());
    if(error.status===401)
    return  Observable.throw(new UnautorizedError('Oturum süreniz sona erdi. İşlem yapabilmek için lütfen tekrar giriş yapınız.'))
    return Observable.throw(new AppError(error));
  }
  constructor(private http: HttpClient,private storage: Storage) { 
   
  }

  getPuantajTableOnthisDate(user:PersonelModel,date:Date){
            
   let  MesaiModel = new GetMesaiModel() ;
   MesaiModel.PersonelID=user.PERSONEL_ID
   MesaiModel.Tarih=date
 
    return this.http.post(this.rootUrl+'api/Mesai/GetThisDate',MesaiModel).map(response =>response).catch(this.handleError)
     
   }

   getLastRecod(user:PersonelModel){
            
    let  MesaiModel = new GetMesaiModel() ;
    MesaiModel.PersonelID=user.PERSONEL_ID

  
     return this.http.post(this.rootUrl+'api/Mesai/LastRecord',MesaiModel).map(response =>response).catch(this.handleError)
      
    }
insertPuantaj( MesaiModel :IDT_IK_MESAI_KAYIT_HAZIRLIK_Model){
  return this.http.post(this.rootUrl+'api/Mesai/InsertMesai',MesaiModel).map(response =>response).catch(this.handleError)
     
}
insertPuantajSef( MesaiModel:Birlesik){
  return this.http.post(this.rootUrl+'api/Mesai/InsertMesaiSef',MesaiModel).map(response =>response).catch(this.handleError)
     
}
CancelOnaySef( model:sefOnayModel){
  return this.http.post(this.rootUrl+'api/Mesai/SefOnayKaldir',model).map(response =>response).catch(this.handleError)
     
}
getSefPersoneli(user:PersonelModel ){
            
  let  MesaiModel = new GetMesaiModel() ;
  MesaiModel.PersonelID=user.PERSONEL_ID
  

   return this.http.post(this.rootUrl+'api/Mesai/SefPersonali',MesaiModel).map(response =>response).catch(this.handleError)
    
  }


}
   

