import { Injectable, ViewChild } from '@angular/core';

import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { BadInput } from '../_helpers/bad-input';
import { NotFoundError } from '../_helpers/not-found-error';
import { UnautorizedError } from '../_helpers/Unauthorized-error';
import { AppError } from '../_helpers/app-error';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch'
import { Storage } from '@ionic/storage';
import { RegisterResponseModel, UserModel, loginmodel } from '../_models/UserModel';


const LoginhttpOptions = {
  headers: new HttpHeaders({
    //original
    //'Content-Type': 'application/x-www-urlencoded', 'No-Auth': 'True',
    'Content-Type': 'application/json', 'No-Auth': 'True',
  })
};
@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {
  readonly rootUrl = 'http://92.45.19.226:80';
  // 'http://  92.45.19.226';
  //readonly rootUrl = 'http://78.189.86.247:118';

  constructor(private http: HttpClient, private storage: Storage) { }

  private handleError(error: HttpErrorResponse) {

    if (error.status === 400)

      return Observable.throw(new BadInput(error.error.Message));

    if (error.status === 404)
      return Observable.throw(new NotFoundError());
    if (error.status === 401)
      return Observable.throw(new UnautorizedError('Oturum süreniz sona erdi. İşlem yapabilmek için lütfen tekrar giriş yapınız.'))
    return Observable.throw(new AppError(error));
  }

  //#region Token AL!! 
  getToken(Tc: string, password: string) {

    let body = new loginmodel();
    body.Tc = Tc;
    body.Password = password;

    return this.http.post(this.rootUrl + '/api/account/login', body, LoginhttpOptions); //.map(user => user).catch(this.handleError)
  }

  getCurrentPersonel() {

    return new Promise(resolve => {
      this.storage.ready().then(() => {

        this.storage.get('currentUser').then(val => {

          resolve(JSON.parse(val))
        }).catch((err) => {
          console.log(err)
        });
      });
    });
  }

  //#endregion

}