import { Component } from '@angular/core';

import { Platform, NavController } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { Storage } from '@ionic/storage';
import { ThemeableBrowser,ThemeableBrowserOptions,ThemeableBrowserObject } from '@ionic-native/themeable-browser/ngx';
@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent {
  public appPages = [
    {
      title: 'Puantaj',
      url: '/home',
      icon: 'home'
    },
     
  ];

  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private navCtrl:NavController,
    private storage: Storage,
    private themeableBrowser :ThemeableBrowser
  ) {
    this.initializeApp();
  }

  
  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }
  LogOut(){
    
    this.splashScreen.hide();
    this.statusBar.styleDefault();
    this.storage.ready().then(() => {
   
    this.storage.remove('currentUser')
     
    this.navCtrl.navigateRoot('');
   
  });
    
  }

  openBordro(){
    const options:ThemeableBrowserOptions={
      toolbar:{
        height:64,
        color:'#423a3a'

      },
      title:{
        color:'#fffffff',
        showPageTitle:true,
        staticText:'Kuşgöz Vinc'
      },
        closeButton:{
          wwwImage:'assets/img/home.png',
          align:'left',
          event:'closePressed'
        },
        backButton:{
          wwwImage:'assets/img/backBtn.png',
          align:'right',
          event:'backPressed'
        }, 
        forwardButton:{
          wwwImage:'assets/img/forwardBtn.png',
          align:'right',
          event:'forwardPressed'
        }

    }
   
    const browser : ThemeableBrowserObject = this.themeableBrowser.create('http://92.45.19.226:90/home/loginBordro','_blank',options)
    browser.on('closePressed').subscribe(res =>{
   browser.close();
    })
  }
  
}
